h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Inter;
  src: url("Inter-Regular.30288fd8.ttf");
}

.header {
  background: linear-gradient(100.83deg, #191536 .67%, #000 100%);
  height: 100vh;
}

@media (width <= 768px) {
  .header {
    height: auto;
  }
}

@media (width <= 425px) {
  .header {
    height: unset;
  }
}

.header__content {
  flex-direction: column;
  justify-content: space-between;
  max-height: 100vh;
  display: flex;
}

@media (width <= 768px) {
  .header__dropdown {
    display: none;
  }
}

.header__top {
  grid-template-columns: repeat(16, 1fr);
  column-gap: 20px;
  display: grid;
}

@media (width <= 1920px) {
  .header__top {
    grid-template-columns: repeat(16, 1fr);
  }
}

@media (width <= 1280px) {
  .header__top {
    grid-template-columns: repeat(12, 1fr);
  }
}

@media (width <= 768px) {
  .header__top {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 24px;
  }
}

@media (width <= 425px) {
  .header__top {
    column-gap: unset;
    grid-template-columns: repeat(2, 1fr);
  }
}

.header__top-left {
  grid-column: 1;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__top-right {
  grid-column: 10 / -1;
  justify-self: center;
  align-items: center;
  display: flex;
}

@media (width <= 1280px) {
  .header__top-right {
    grid-column: 6 / -1;
  }
}

@media (width <= 1024px) {
  .header__top-right {
    grid-column: 4 / -1;
  }
}

@media (width <= 768px) {
  .header__top-right {
    grid-column: 6 / -1;
  }
}

.header__logo {
  transition: transform .3s;
}

.header__logo:hover {
  transform: scale(.9);
}

.header__nav-link {
  color: #fff;
  transition: color .3s;
}

.header__nav-link:hover {
  color: #05c2df;
}

@media (width <= 768px) {
  .header__nav-link {
    display: none;
  }
}

@media (width <= 1024px) {
  .header__button--tablet {
    display: none;
  }
}

@media (width <= 768px) {
  .header__button {
    display: none;
  }
}

.header__menu--icon {
  opacity: 0;
  background-image: url("menu.ae3a927c.svg");
  background-size: cover;
  width: 20px;
  height: 20px;
  transition: transform .3s;
  display: block;
}

@media (width <= 768px) {
  .header__menu--icon {
    opacity: 1;
  }
}

.header__menu--icon:hover {
  transform: scale(1.2);
}

.header__center {
  grid-template-columns: repeat(16, 1fr);
  column-gap: 20px;
  height: 70vh;
  display: grid;
}

@media (width <= 1920px) {
  .header__center {
    grid-template-columns: repeat(16, 1fr);
  }
}

@media (width <= 1280px) {
  .header__center {
    grid-template-columns: repeat(12, 1fr);
  }
}

@media (width <= 768px) {
  .header__center {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 24px;
  }
}

@media (width <= 425px) {
  .header__center {
    column-gap: unset;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width <= 768px) {
  .header__center {
    place-content: center;
    height: auto;
    margin-top: 3.4rem;
  }
}

@media (width <= 425px) {
  .header__center {
    flex-direction: column-reverse;
    height: 100%;
    margin-top: 1rem;
    display: flex;
  }
}

.header__center-image {
  position: relative;
}

.header__center-image:before {
  content: "";
  background-color: #07798b;
  width: .23rem;
  height: .13rem;
  display: block;
  position: absolute;
  overflow-y: hidden;
}

.header__center-left {
  flex-direction: column;
  grid-column: span 7;
  justify-content: space-evenly;
  align-items: flex-start;
  display: flex;
}

@media (width <= 1280px) {
  .header__center-left {
    grid-column: span 6;
  }
}

@media (width <= 768px) {
  .header__center-left {
    grid-column: span 3;
    gap: .6rem;
  }
}

.header__center-left > :not(:first-child) {
  margin-left: 2rem;
}

@media (width <= 768px) {
  .header__center-left > :not(:first-child) {
    margin: 0;
  }
}

.header__center-right {
  grid-column: 8 / -1;
}

@media (width <= 1280px) {
  .header__center-right {
    grid-column: 7 / -1;
  }
}

@media (width <= 1024px) {
  .header__center-right {
    align-self: center;
  }
}

@media (width <= 768px) {
  .header__center-right {
    grid-column: 4 / -1;
  }
}

@media (width <= 425px) {
  .header__center-right {
    align-self: auto;
    height: 10.75rem;
  }
}

.header__center-right > img {
  object-fit: contain;
  background-position: center;
  width: 50.67rem;
  height: 43rem;
}

@media (width <= 768px) {
  .header__bottom {
    display: none;
  }
}

.header__title {
  text-align: left;
  color: #fff;
  white-space: nowrap;
  margin-top: 20px;
  font-weight: 900;
}

.header__title--span {
  color: #05c2df;
  font-weight: 400;
}

@media (width <= 768px) {
  .header__title--span {
    font-weight: 400;
  }
}

.header__paragraph {
  color: #929292;
  text-align: left;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}

.header__paragraph + span {
  color: #fff;
}

@media (width <= 425px) {
  .header__paragraph + span {
    align-self: center;
  }
}

.modal {
  z-index: 8;
  background-color: #00000080;
  width: 100%;
  height: 100%;
  transition: transform .3s ease-in;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  transform: translateX(-100%);
}

.modal[style*="display: block"] {
  transform: translateX(0);
}

.modal__title {
  text-align: left;
}

.modal__title--margin {
  margin-right: 18.5rem;
}

@media (width <= 425px) {
  .modal__title--margin {
    align-self: flex-start;
    margin-right: 0;
  }
}

.modal__span {
  color: #05c2df;
}

.modal__content {
  background-color: #191536;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  margin: 10% auto;
  padding-block: 2.3rem;
  padding-inline: 5rem;
  display: flex;
  position: relative;
}

@media (width <= 768px) {
  .modal__content {
    width: 100%;
    margin: 0 auto;
  }
}

@media (width <= 425px) {
  .modal__content {
    height: 100%;
    padding-inline: 1.3rem;
    overflow-y: auto;
  }
}

.modal__close {
  filter: invert();
  padding: 1rem;
  transition: transform .5s ease-in-out, filter .1s;
  position: absolute;
  top: 1.5rem;
  right: 1rem;
}

.modal__close--order--position {
  position: unset;
  padding: 0;
}

.modal__close:hover {
  filter: invert(74%) sepia(24%) saturate(6845%) hue-rotate(142deg) brightness(91%) contrast(96%);
  cursor: pointer;
  transform-origin: center;
  transform: rotateX(180deg);
}

.modal__back {
  transition: transform .5s ease-in-out;
  position: absolute;
  top: 5%;
  left: 7%;
}

.modal__back:hover {
  filter: invert(74%) sepia(24%) saturate(6845%) hue-rotate(142deg) brightness(91%) contrast(96%);
  cursor: pointer;
  transform-origin: center;
  transform: rotateX(180deg);
}

@media (width <= 425px) {
  .modal {
    z-index: 8;
    height: 100%;
  }
}

.accordion {
  width: 100%;
  max-width: 56rem;
  padding-block: 1.25rem;
}

.accordion__icon--right {
  background-image: url("arrow-right-blue.5841f31c.svg");
  background-repeat: no-repeat;
  width: .56rem;
  height: .38rem;
  display: block;
  transform: rotate(-90deg);
}

.accordion__icon--down {
  background-image: url("dropdown.09ac9dd7.svg");
  background-repeat: no-repeat;
  width: .56rem;
  height: .38rem;
  display: none;
}

.accordion__content {
  background: none;
  border: .13rem solid #05c2df;
  border-radius: .25rem;
  margin: .7rem 0;
  overflow: hidden;
}

.accordion__content.open {
  padding-bottom: .63rem;
}

.accordion__header {
  cursor: pointer;
  justify-content: space-between;
  align-items: center;
  min-height: 3.13rem;
  padding: 0 1rem;
  transition: all .2s linear;
  display: flex;
}

.accordion__title {
  color: #05c2df;
}

.accordion__description {
  color: #929292;
  height: 0;
  margin-bottom: 0;
  padding: 0 1rem;
  transition: all .2s linear;
}

.accordion__info {
  color: #929292;
  height: 0;
  padding: 0 1rem;
  font-size: .88rem;
  transition: all .2s linear;
  display: block;
}

.container {
  max-width: 1920px;
  margin: 0 auto;
  padding: 20px;
}

@media (width <= 1024px) {
  .container {
    margin-inline: auto;
    padding-block: 60px;
    padding-inline: 220px;
  }
}

@media (width <= 1280px) {
  .container {
    margin-inline: auto;
    padding-block: 60px;
    padding-inline: 110px;
  }
}

@media (width <= 768px) {
  .container {
    margin: 0;
    padding-block: 70px;
    padding-inline: 30px;
  }

  .container--header--tablet {
    padding-block: 30px;
  }
}

@media (width >= 320px) and (width <= 425px) {
  .container {
    padding: 1.25rem;
  }
}

.custom-dropdown {
  cursor: pointer;
  padding-inline: 1rem;
  display: inline-block;
  position: relative;
}

.custom-dropdown--m {
  padding: 0;
}

.custom-dropdown--l {
  padding: 0;
  width: 100%;
  margin-inline: .5rem;
  padding-top: .5rem;
}

.selected-option {
  color: #05c2df;
  cursor: pointer;
  font: inherit;
  background: none;
  justify-content: space-evenly;
  align-items: center;
  width: 2.81rem;
  display: flex;
}

.selected-option:after {
  content: "";
  background-image: url("dropdown.09ac9dd7.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 10px;
  height: 10px;
  padding-top: 8px;
}

.selected-option:before {
  content: "";
  transform-origin: 0;
  background-color: #fff;
  width: 45%;
  height: .13rem;
  transition: transform .3s ease-in-out;
  display: block;
  position: absolute;
  bottom: 1.25rem;
  left: 50px;
  transform: scale(0);
}

.selected-option:hover:before {
  transform: scale(1);
}

.selected-option--m {
  color: #fff;
  background-color: #191536;
  width: 5.63rem;
  height: 2.94rem;
}

.selected-option--m:before {
  opacity: 0;
}

.selected-option--l {
  color: #fff;
  background-color: #191536;
  justify-content: space-between;
  width: 100%;
  max-width: 21.63rem;
  height: 2.94rem;
  padding-inline: 1.3rem;
}

.selected-option--l:before {
  opacity: 0;
}

.options {
  color: #fff;
  background-color: #191536;
  border-radius: 4px;
  height: 10vh;
  margin-top: .13rem;
  display: none;
  position: absolute;
  top: 100%;
  overflow-y: scroll;
}

.options::-webkit-scrollbar {
  width: .1rem;
}

.options::-webkit-scrollbar-track {
  background-color: #0000;
}

.options::-webkit-scrollbar-thumb {
  background-color: #05c2df;
}

.options--m {
  width: 100%;
}

@media (width <= 425px) {
  .options--m {
    z-index: 2;
  }
}

.options--l {
  width: 100%;
  height: 15vh;
}

@media (width <= 425px) {
  .options--l {
    z-index: 2;
  }
}

.option {
  cursor: pointer;
  text-align: center;
  padding: .4rem;
}

.option--m {
  padding: 1rem;
}

.option--l {
  text-align: left;
  padding-inline: 1.3rem;
}

.option:hover {
  color: #05c2df;
}

.main-button {
  text-align: center;
  color: #fff;
  background-color: #05c2df;
  border: none;
  border-radius: 4px;
  outline: none;
  width: 200px;
  height: 48px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
}

.main-button:hover {
  cursor: pointer;
  color: #05c2df;
  background-color: #fff;
}

@media (width <= 768px) {
  .main-button--tablet {
    z-index: 8;
    width: 60vw;
    display: block;
    position: fixed;
    bottom: 5vw;
    left: 20vw;
  }
}

.nav {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.nav__list {
  color: #fff;
  padding: 5px;
  list-style: none;
  display: flex;
}

.nav__list-item {
  padding-inline: 20px;
  position: relative;
}

.nav__list-item:after {
  content: "";
  transform-origin: 0;
  background-color: #fff;
  width: 60%;
  height: .13rem;
  transition: transform .3s ease-in-out;
  display: block;
  position: absolute;
  bottom: -.5rem;
  left: 20%;
  transform: scale(0);
}

.nav__list-item:hover:after {
  transform: scale(1);
}

.nav__list-link {
  color: #fff;
  transition: color .3s;
}

.nav__list-link:hover {
  color: #05c2df;
}

.main {
  overflow: hidden;
}

.main__section:nth-child(2n+2) {
  background: linear-gradient(100.83deg, #191536 .67%, #000 100%);
}

.main__section:nth-child(n+1) {
  background-color: #05040b;
}

.main__section-title {
  text-transform: uppercase;
  padding: 10px;
}

.main__section-span {
  color: #05c2df;
  font-weight: 400;
}

.main__section-span--custom--gaming {
  background-image: url("gaming.84e596e0.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 10.02rem;
  height: 1.59rem;
  display: inline-block;
}

@media (width <= 768px) {
  .main__section-span--custom--gaming {
    background-size: cover;
    width: 7.02rem;
    height: 1.1rem;
    display: inline-block;
  }
}

.main__section-span--custom--why {
  background-image: url("why.6e8add82.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 15rem;
  height: 1.59rem;
  display: inline-block;
}

@media (width <= 768px) {
  .main__section-span--custom--why {
    background-size: contain;
    width: 9rem;
    height: 1.01rem;
    display: inline-block;
  }
}

.main__section-paragraph {
  line-height: 150%;
}

.about {
  height: 100%;
}

.about__top {
  grid-template-rows: 1fr 3rem;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 1rem;
  min-width: 0;
  display: grid;
  position: relative;
}

.about__top-text {
  grid-area: 1 / 3 / auto / -1;
  margin-top: 12rem;
}

@media (width <= 425px) {
  .about__top-text {
    order: 3;
  }

  .about__top {
    flex-direction: column;
    row-gap: 1rem;
    display: flex;
  }
}

.about__title {
  text-align: left;
  padding-bottom: 1.31rem;
  font-weight: 900;
}

@media (width <= 425px) {
  .about__title {
    order: 3;
    display: none;
  }
}

.about__title--mobile {
  display: none;
}

@media (width <= 425px) {
  .about__title--mobile {
    order: 1;
    display: block;
  }
}

.about__paragraph {
  color: #929292;
  width: 20.75rem;
}

@media (width <= 425px) {
  .about__paragraph {
    width: 100%;
  }
}

.about__bottom {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 12rem;
  display: flex;
}

@media (width <= 768px) {
  .about__bottom {
    margin-top: 6rem;
  }
}

.about__bottom-span {
  color: #05c2df;
  font-weight: 400;
}

.about__bottom-title {
  padding-block: 1.5rem;
}

.about__bottom-paragraph {
  text-align: left;
  color: #929292;
}

.about__bottom-images {
  height: 21rem;
  display: flex;
}

@media (width <= 425px) {
  .about__bottom-images {
    display: none;
  }
}

.about__bottom-image-span {
  white-space: pre;
  color: #0000;
  -webkit-text-stroke: .1rem #05c2df;
  flex-direction: column;
  justify-content: center;
  place-items: center;
  width: 15vh;
  font-size: 4rem;
  font-weight: 900;
  line-height: 130%;
  transition: opacity .3s;
  display: flex;
  transform: rotate(90deg);
}

.about__bottom-image-span:nth-child(2n) {
  transform: rotate(270deg);
}

.about__bottom-image-span:first-child {
  opacity: 1;
}

.about__bottom-image-span:nth-child(2) {
  opacity: .7;
}

.about__bottom-image-span:nth-child(3) {
  opacity: .4;
}

.about__bottom-image-span:nth-child(4) {
  opacity: .1;
}

.about__bottom-image-span:nth-child(5) {
  opacity: -.2;
}

.about__bottom-image-span:hover {
  opacity: 1;
}

@media (width <= 1024px) {
  .about__bottom-image-span:nth-child(n+5) {
    display: none;
  }
}

@media (width <= 768px) {
  .about__bottom-image-span {
    font-size: 3rem;
  }

  .about__bottom-image-span:nth-child(n+4) {
    display: none;
  }
}

@media (width <= 650px) {
  .about__bottom-image-span:nth-child(n+3) {
    display: none;
  }
}

@media (width <= 550px) {
  .about__bottom-image-span:nth-child(n+2) {
    display: none;
  }
}

.slider-pagination__fraction {
  color: #05c2df;
  z-index: 3;
  grid-row: 1;
  justify-self: end;
  display: flex;
}

@media (width <= 768px) {
  .slider-pagination__fraction {
    display: none;
  }
}

.about-swiper {
  grid-area: 1 / 1 / auto / 2;
  max-height: 100%;
  position: relative;
}

@media (width <= 1920px) {
  .about-swiper {
    margin: auto;
  }
}

@media (width <= 425px) {
  .about-swiper {
    order: 2;
    width: 17.5rem;
    height: 11.81rem;
  }
}

.about__top-swipper-slide {
  display: flex;
}

.about__top-swiper-slide img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: inline-block;
}

@media (width <= 768px) {
  .about__top-swiper-slide img {
    object-fit: cover;
  }
}

.about-progressbar.swiper-pagination-horizontal {
  width: 200px;
  height: 1px;
  top: 100%;
  overflow: hidden;
}

.about-progressbar.swiper-pagination-horizontal span {
  background-color: #05c2df;
}

.about__top-navigation-slider {
  color: #fff;
  cursor: pointer;
  grid-area: 2 / 1 / auto / 2;
  align-items: center;
  gap: 3rem;
  display: flex;
  position: relative;
}

@media (width <= 1024px) {
  .about__top-navigation-slider {
    display: none;
  }
}

.about-swiper ~ .swiper-about-bullets {
  z-index: 2;
  order: 3;
  justify-content: center;
  display: none;
  position: static;
}

@media (width <= 1024px) {
  .about-swiper ~ .swiper-about-bullets {
    grid-column: 2;
    align-self: center;
    display: flex;
  }
}

.swiper-pagination-bullet {
  background-color: #fff;
}

@media (width <= 425px) {
  .swiper-pagination-bullet {
    width: .25rem;
    height: .25rem;
  }
}

.article {
  cursor: pointer;
  flex-direction: column;
  place-items: center;
  padding: 1.2rem;
  transition: transform .3s;
  display: flex;
  overflow: hidden;
}

@media (width <= 425px) {
  .article {
    align-items: unset;
    justify-items: unset;
    padding: 0;
  }
}

.article__img {
  margin-bottom: 2rem;
}

@media (width <= 425px) {
  .article__img {
    display: none;
  }
}

.article__title {
  white-space: nowrap;
  margin-bottom: 1rem;
}

.article__paragraph {
  text-align: center;
  color: #929292;
  line-height: 150%;
  transition: color .3s ease-in-out;
}

@media (width <= 425px) {
  .article__paragraph {
    text-align: left;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    margin-bottom: 1rem;
    display: -webkit-box;
    overflow: hidden;
  }
}

.article:hover {
  transform: scale(1.1);
}

.article:hover .article__paragraph {
  color: #fff;
}

@media (width <= 425px) {
  .header-swiper {
    height: 100%;
  }
}

.header__swiper-slide {
  background-position: center;
  background-repeat: no-repeat;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__swiper-slide img {
  object-fit: contain;
  width: 100%;
  max-width: max-content;
  height: 100%;
